import express from "express";
import path from "path";
const app = express();
const port = 5000;
import { getTodo } from "./controller/todoController/getTodo";
import { addTodo } from "./controller/todoController/addTodo";
import { updateTodo } from "./controller/todoController/updateTodo";
import { deleteTodo } from "./controller/todoController/deleteTodo";
import { addNewUser } from "./controller/userController/addNewUser";

app.use(express.json());

app.use("/static", express.static(path.join(__dirname, "../dist")));

app.get("/todo", getTodo);

app.post("/todo", addTodo);

app.put("/todo", updateTodo);

app.delete("/todo", deleteTodo);

app.post("/user", addNewUser);

app.get("/*", (req, res) =>
  res.sendFile(path.join(__dirname, "../public/index.html"))
);

app.listen(port, () =>
  console.log(`Example app listening at http://localhost:${port}`)
);
