import config from "../../config";
import axios from "axios";

export const getTodo = async (req, res) => {
  try {
    const {
      JSONBinBaseURL,
      headers: { secretKey },
    } = config;

    const result = await axios.get(JSONBinBaseURL, {
      headers: { "secret-key": secretKey },
    });

    res.send(result.data);
  } catch (error) {
    console.log(error);
  }
};
