import config from "../../config";
import axios from "axios";

export const addTodo = async (req, res) => {
  try {
    const {
      JSONBinBaseURL,
      headers: { secretKey, contentType, versioning },
    } = config;

    await axios.put(JSONBinBaseURL, req.body.items, {
      headers: {
        "secret-key": secretKey,
        "Content-Type": contentType,
        versioning,
      },
    });

    res.sendStatus(200);
    // console.log(result);

    // console.log(req.body);
  } catch (error) {
    console.log(error);
    res.sendStatus(500);
  }
};
