import config from "../../config";
import axios from "axios";

export const updateTodo = async (req, res) => {
  try {
    const {
      JSONBinBaseURL,
      headers: { secretKey, contentType, versioning },
    } = config;

    // console.log(req.body);

    await axios.put(JSONBinBaseURL, req.body, {
      headers: {
        "secret-key": secretKey,
        "Content-Type": contentType,
        versioning,
      },
    });

    res.sendStatus(200);
  } catch (error) {
    console.log(error);
  }
};
